.App{
  font-family: sans-serif;
  text-align: center;
}

.heading{
  color: crimson;
}

.card{
  background-color: white;
  border-radius: 1.25rem;
  padding: 1rem;
  margin-bottom: 0.5rem;
}

body{
  background-color: skyblue;
}

.features {
   /*list-style: none;*/
   text-align: left;
   border-top: 1px solid rgb(212,209,209);
   padding-top: 0.75rem;
}

.features>li {
  margin-bottom: 0.5rem;
  font-size: 0.8rem;
}

.features > li::marker {
  content: "✔️";
}

.card:nth-child(1) .features > li:nth-child(n+5)::marker {
  content: "❌";
}

.card:nth-child(1) .features > li:nth-child(n + 5) {
    opacity: 0.6;
}

.card:nth-child(2) .features>li:last-child {
  opacity: 0.6;
}

.card:nth-child(2) .features > li:last-child::marker {
  content: "❌";
}

.buy {
  width: 100%;
  background-color: #0d6efd;
  border-color:#0d6efd;
  color: white;
  border-radius: 1000px;
  padding: 0.75rem;
  font-size: 0.75rem;
  letter-spacing: 4px;
  opacity: 0.7;
}

.buy:hover{
  background-color: #0b5ed7;
  border-color: #0a58ca;
}

.price{
  font-size: 2rem;
  margin: 1rem 0;
}

.price-duration{
  font-size: 0.6rem;
}

.plan-name{
  color: slategray;
  font-size: 0.8rem;
  letter-spacing: 1.5px;
}